/*#################__STYLE__#################*/

nav#navbar {
    position: fixed;
    top: 0;
    z-index: 99;
    width: 100%;
    padding: 10px 0;

    div.nav-container {
        display: flex;
        justify-content: center;

        div.nav-items {
            padding: 10px 7px;
            background-color: rgba(255, 255, 255, 0.5);
            border-radius: 5px;
            display: grid;
            grid-template-columns: auto auto auto auto;
            justify-content: center;
            align-items: center;
            -webkit-box-shadow: 0px 0px 14px 5px rgba(0, 0, 0, 0.2);
            box-shadow: 0px 0px 14px 5px rgba(0, 0, 0, 0.2);
            border: 1px solid var(--orange);

            a.nav-link {
                display: inline-block;
                border-radius: 3px;
                font-size: 18px;
                text-decoration: none;
                color: var(--white);
                font-weight: 700;
                padding: 0 3px;
                background: url("../images/t.gif");
                transition: letter-spacing 0.3s;
                margin: 2px 2px;
                text-align: center;

                span {
                    color: var(--orange);
                }

                &:hover {
                    letter-spacing: 2px;
                }
            }
        }
    }
}

/*#################__RESPONSIVE__#################*/

@media only screen and (max-width: 450px) {
    nav#navbar {
        position: absolute;
        padding: 20px 0;

        div.nav-container {
            div.nav-items {
                grid-template-columns: 45% 45%;
                border-radius: 0;
                width: 100%;
                padding: 5px 7px;
                background-color: transparent;
                -webkit-box-shadow: none;
                box-shadow: none;
                border: none;
            }
        }
    }
}

@media only screen and (max-width: 350px) {
    nav#navbar {
        div.nav-container {
            div.nav-items {
                grid-template-columns: 60%;
                padding: 5px;

                a.nav-link {
                    margin: 2px 0;
                }
            }
        }
    }
}

@media only screen and (max-width: 252px) {
    nav#navbar {
        div.nav-container {
            div.nav-items {
                grid-template-columns: 90%;
            }
        }
    }
}

@media only screen and (max-width: 180px) {
    nav#navbar {
        div.nav-container {
            div.nav-items {
                grid-template-columns: 100%;

                a.nav-link {
                    font-size: 16px;
                }
            }
        }
    }
}
